@import '../../styles/utils/mixins';

.card-slider-skeleton {
  max-width: calc(100vw);
  width: 100%;

  height: 440px;

  padding-left: 16px;

  @include onTablet {
    padding-left: 24px;
  }

  @include onDesktop {
    padding-left: 0;
    max-width: 1200px;
    margin-inline: auto;
  }

  @include onTablet {
    height: 505px;
  }

  display: flex;
  gap: 16px;
  justify-content: space-between;
  overflow: hidden;

  &__item-container {
    min-width: 210px;
    height: 100%;

    @include onTablet {
      min-width: 240px;
    }

    @include onDesktop {
      min-width: 270px;
    }

    flex: 1;
  }
}
